.title h1{
  color: #fff;
  text-align: center;
}

.projects{
  margin: 20px 5%;
  display: grid;
  grid-template-columns: repeat(3, 2fr);
  grid-gap: 5px;
}

@media screen and (max-width: 960px) {
  .projects{
    width: 90%;
    display: inline-block;
    align-content: center;
  }
}